import { Injectable } from '@angular/core';
import { Subject } from 'rxjs';
import { AppConstants } from './common/application.constant';
import { HttpClient, HttpHeaders } from '@angular/common/http';

@Injectable({
  providedIn: 'root'
})
export class AppService {

  public requestStatusEvent$: Subject<any> = new Subject();
  public payOffEvent$: Subject<any> = new Subject();

  constructor(
    private http: HttpClient,
  ) { }

  get(urlToFetch) {
    let httpOptions = {};

    let url = urlToFetch;
    return this.http.get(url, httpOptions);
  }
}
