  getData1() {
    console.log('app getting data1');
    this.appService.get(AppConstants.wazirxTicker).subscribe((res: any) => {
      console.log('app data1', res);
      if (res) {
        AppConstants.wazirxQuotesInr = [];
        AppConstants.wazirxQuotesBtc = [];
        AppConstants.wazirxQuotesUsdt = [];
        AppConstants.wazirxQuotesWrx = [];
        for (let key in res) {
          // console.log(res[key]);

          let value = res[key];
          let e = new QuoteData();
          e.currency = value.base_unit;
          e.buy = value.buy;
          e.sell = value.sell;
          if (value.quote_unit === 'inr') {
            AppConstants.wazirxQuotesInr.push(e);
          }
          if (value.quote_unit === 'btc') {
            AppConstants.wazirxQuotesBtc.push(e);
          }
          if (value.quote_unit === 'usdt') {
            AppConstants.wazirxQuotesUsdt.push(e);
          }
          if (value.quote_unit === 'wrx') {
            AppConstants.wazirxQuotesWrx.push(e);
          }
        }
        console.log('wazirx inr pairs', AppConstants.wazirxQuotesInr);
        console.log('wazirx btc pairs', AppConstants.wazirxQuotesBtc);
        console.log('wazirx usdt pairs', AppConstants.wazirxQuotesUsdt);
        console.log('wazirx wrx pairs', AppConstants.wazirxQuotesWrx);

        this.triangle();

        this.saveToLocalStorage();
      }
    },
    error => {
      console.log('app data1 error', error);
      this.appService.requestStatusEvent$.next(error.error.message +  ', ' + error.message);
    });
  }

  triangle() {
    let initialAmount = 100000;

    this.arbitrage = [];
    let a1: ArbitrageData[] = this.doTriangle(initialAmount, 'inr', AppConstants.wazirxQuotesInr, 'usdt', AppConstants.wazirxQuotesUsdt);
    this.arbitrage = this.arbitrage.concat(a1);
    let a2: ArbitrageData[] = this.doTriangle(initialAmount, 'inr', AppConstants.wazirxQuotesInr, 'btc', AppConstants.wazirxQuotesBtc);
    this.arbitrage = this.arbitrage.concat(a2);
    let a3: ArbitrageData[] = this.doTriangle(initialAmount, 'inr', AppConstants.wazirxQuotesInr, 'wrx', AppConstants.wazirxQuotesWrx);
    this.arbitrage = this.arbitrage.concat(a3);
    let a4: ArbitrageData[] = this.doTriangle(initialAmount, 'usdt', AppConstants.wazirxQuotesUsdt, 'btc', AppConstants.wazirxQuotesBtc);
    this.arbitrage = this.arbitrage.concat(a4);
    let a5: ArbitrageData[] = this.doTriangle(initialAmount, 'usdt', AppConstants.wazirxQuotesUsdt, 'wrx', AppConstants.wazirxQuotesWrx);
    this.arbitrage = this.arbitrage.concat(a5);
    let a6: ArbitrageData[] = this.doTriangle(initialAmount, 'btc', AppConstants.wazirxQuotesBtc, 'wrx', AppConstants.wazirxQuotesWrx);
    this.arbitrage = this.arbitrage.concat(a6);

    this.arbitrage = this.arbitrage.filter(e => !isNaN(e.roi) && e.roi !== Infinity);

    this.arbitrage.sort((a, b) => {
      if (a.roi > b.roi) {
        return -1;
      } else {
        return 1;
      }
      /* if (Math.abs(a.roi) > Math.abs(b.roi)) {
        return -1;
      } else {
        return 1;
      }  */  
    });

    this.arbitrage = this.arbitrage.filter((thing, index, self) =>
      index === self.findIndex((t) => (
        t.pair === thing.pair && t.currency1 === thing.currency1 && t.currency2 === thing.currency2
      ))
    );

    //this.arbitrage = this.arbitrage.filter((v,i,a)=>a.findIndex(t=>(t.currency1 === v.currency2 && t.pair===v.pair))===i);


    console.log(this.arbitrage);

  }

  doTriangle(initialAmount, firstCurr, firstQuotes, secondCurr, secondQuotes): ArbitrageData[] {
    let arbitrageLocal: ArbitrageData[] = [];
    firstQuotes.forEach((q: QuoteData) => {
      let curr = q.currency;
      /* if (curr !== 'btc') {
        return;
      } */
      let firstBuyRate = q.sell;
      let buyQty1 = initialAmount / firstBuyRate;
      console.log('curr', curr);

      if (curr === 'usdt') {
        arbitrageLocal = this.getNextIteration(AppConstants.wazirxQuotesUsdt, curr, initialAmount, firstQuotes, buyQty1, firstCurr, firstBuyRate, arbitrageLocal);
      } else if (curr === 'btc') {
        arbitrageLocal = this.getNextIteration(AppConstants.wazirxQuotesBtc, curr, initialAmount, firstQuotes, buyQty1, firstCurr, firstBuyRate, arbitrageLocal);
      } else if (curr === 'wrx') {
        arbitrageLocal = this.getNextIteration(AppConstants.wazirxQuotesWrx, curr, initialAmount, firstQuotes, buyQty1, firstCurr, firstBuyRate, arbitrageLocal);
      }

      let secondQuotesCurr = secondQuotes.find(x => x.currency === curr);
      console.log('secondQuotesCurr', secondQuotesCurr);
      if (!secondQuotesCurr) {
        return;
      }
      let buyQty2 = secondQuotesCurr.buy * buyQty1;

      let secondCurrQuoteFirst = firstQuotes.find(x => x.currency === secondCurr);
      console.log('secondCurrQuoteFirst', secondCurrQuoteFirst);
      if (!secondCurrQuoteFirst) {
        return;
      }
      let sellFinal = secondCurrQuoteFirst.buy * buyQty2;

      let roi = ((sellFinal - initialAmount) / initialAmount) * 100;
      console.log(sellFinal, roi);
      
      let a = new ArbitrageData();
      a.pair = firstCurr;
      a.currency1 = curr;
      a.rate1 = firstBuyRate;
      a.qty1 = Math.round((buyQty1 + 0.00001) * 10000) / 10000;
      a.currency2 = secondCurr;
      a.rate2 = secondQuotesCurr.buy;
      a.qty2 = Math.round((buyQty2 + 0.00001) * 10000) / 10000;
      a.rate3 = secondCurrQuoteFirst.buy;
      a.finalAmount = Math.round((sellFinal + 0.00001) * 100) / 100;
      a.roi = Math.round((roi + 0.00001) * 100) / 100;
      arbitrageLocal.push(a);
      

    });

    return arbitrageLocal;
  }

  getNextIteration(arr, curr, initialAmount, firstQuotes, buyQty1, firstCurr, firstBuyRate, arbitrageLocal) {

    arr.forEach((q1: QuoteData) => {
      let buyQty2 = buyQty1 / q1.sell;

      let secondCurrQuoteFirst = firstQuotes.find(x => x.currency === q1.currency);
      console.log('secondCurrQuoteFirst', secondCurrQuoteFirst, buyQty1, buyQty2);
      if (!secondCurrQuoteFirst) {
        return;
      }
      let sellFinal = secondCurrQuoteFirst.buy * buyQty2;
     
      let roi = ((sellFinal - initialAmount) / initialAmount) * 100;
      console.log(sellFinal, roi);
      
      let a = new ArbitrageData();
      a.pair = firstCurr;
      a.currency1 = curr;
      a.rate1 = firstBuyRate;
      a.qty1 = Math.round((buyQty1 + 0.00001) * 10000) / 10000;
      a.currency2 = q1.currency;
      a.rate2 = q1.sell;
      a.qty2 = Math.round((buyQty2 + 0.00001) * 10000) / 10000;
      a.rate3 = secondCurrQuoteFirst.buy;
      a.finalAmount = Math.round((sellFinal + 0.00001) * 100) / 100;
      a.roi = Math.round((roi + 0.00001) * 100) / 100;
      arbitrageLocal.push(a);
    });
    return arbitrageLocal;
  }

  saveToLocalStorage() {

    let nowDate = new Date().toLocaleString();
    console.log('nowDate', nowDate);
    
    this.arbitrage.forEach(e => {
      let key = e.pair + '-' + e.currency1 + '-' + e.currency2;
      let val = [nowDate, e.roi];

      let existing = JSON.parse(localStorage.getItem(key));
      if (!existing) {
        existing = [];
      }
      existing.push(val);

      localStorage.setItem(
        key,
        JSON.stringify(existing)
      );
    });
    
  }

